import { resolve } from 'app/js/adapters/adapters';
import Marionette from 'app/js/initializers/extensions/MarionetteExtension';
// @ts-expect-error ts-migrate(2307) FIXME: Cannot find module 'app/templates/LogsV... Remove this comment to see the full error message
import tpl from 'app/templates/LogsView.tpl';

const LogsView = Marionette.ItemView.extend({
  //
  // Properties

  className: 'logs-container',
  template: tpl,

  ui: {
    textarea: 'textarea',
  },

  collectionEvents: {
    add: 'onAdd',
  },

  onlyAfterRender: ['onAdd'],

  //
  // Init

  initialize: function () {
    this.collection = resolve('logs');
  },

  //
  // Control

  serializeData: function () {
    return { logs: this.collection.toJSONString() };
  },

  //
  // Listeners

  onAdd: function () {
    this.ui.textarea.text(this.collection.toJSONString());
  },
});

export default LogsView;
